
header{
    position: fixed;
    width: 100%;
    display: block;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    background: var(--main-color);
    /* box-shadow: 3px 10px 100px rgba(0, 0, 0, 0.06); */
}

header .nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 150px;
}

header .logo img{
    width: 9.5rem;
}

header .navbar{
    display: flex;
    column-gap: 1.5rem;
}

header .navbar a:active{
    color: var(--main-color);
}

.headerActive{
    color: var(--bx);
    background-color: var(--main-color);
    transition: all 0.3s;
}

header .userControl{
    display: flex;
    column-gap: 1.8rem;
    /* background-color: var(--btn); */
}

header .userControl .notification{
    position: relative;
}

header .userControl .notification .bx{
    color: var(--text-color);
    font-size: 1.5rem;
    margin-top: 8px;
}

header .userControl .notification span{
    /* z-index: -1; */
    /* display: inline-block; */
    color: var(--text-color);
    position: absolute;
    top: -20%;
    left: 50%;
    background-color: red;
    min-width: 2rem;
    text-align: center;
    padding: 0 0.2rem 0.2rem 0;
    border-radius: 1rem;
    opacity: 0;
}

header .navbar, .user{
    color: var(--text-color);
    font-size: 1.2rem;
}

.user{
    display: flex;
    /* gap: 1rem; */
}

.user div{
    height: 2.5rem;
    padding-top: 0.5rem;
    /* background-color: gray; */
    position: absolute;
    right: 6rem;
    padding-left: 1rem;
}


.user button, .user img{
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 3rem;
    object-fit: cover;
    z-index: 1;
}

.bx{
    font-size: 1.5rem;
    cursor: pointer;
}


header .navbar .active{
    color: var(--bx);
    background-color: var(--main-color);
}

